	module.exports = {"data":{"markdownRemark":{"html":"<p>Search Preview offers a drag and drop interface for building and testing your search Ux visually.</p>\n<p><img src=\"https://i.imgur.com/DxBfkBS.png\"></p>\n<h2 id=\"how-search-preview-works\"><a href=\"#how-search-preview-works\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>How Search Preview Works</h2>\n<p>Search Preview allows configuring the search, filtering and result options to build and test the search UI visually.</p>\n<h3 id=\"search-settings\"><a href=\"#search-settings\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Search Settings</h3>\n<p>You can configure the search to set which fields should the search apply on, including the weights associated with the fields, set / unset highlighting of results and auto suggestions, typo tolerance and whether to match all terms or any terms.</p>\n<p><img src=\"https://i.imgur.com/tTKAIVh.png\"></p>\n<h3 id=\"filter-settings\"><a href=\"#filter-settings\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Filter Settings</h3>\n<p>When adding a new filter option, you can select the field on which to apply the filter, how many values to show in the list, whether to display the count values, how to sort the list by, and whether to allow single or multiple selections.</p>\n<p><img src=\"https://i.imgur.com/dZGL7hP.png\"></p>\n<h3 id=\"result-settings\"><a href=\"#result-settings\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Result Settings</h3>\n<p>When editing the result options, you can decide what field to sort the results by, how many items to show in a page, whether to show pagination or use infinite scroll as well as configuring which fields should show up in the results.</p>\n<p><img src=\"https://i.imgur.com/qTat6n2.png\"></p>\n<h2 id=\"exporting-search-code\"><a href=\"#exporting-search-code\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Exporting Search Code</h2>\n<p>While you are visually building the UI, Search Preview auto generates the codebase that can be previewed individually for each component or exported directly to Codesandbox.</p>\n<p><img src=\"https://i.imgur.com/uhjqc3W.png\"></p>\n<p><img src=\"https://i.imgur.com/QlIgoHd.png\"></p>\n<p><img src=\"https://i.imgur.com/PrHmwJD.png\"></p>","frontmatter":{"title":"Search Preview","next":"analytics.html","prev":"mappings.html","nextTitle":"Analytics","prevTitle":"Mappings"},"fields":{"path":"docs/concepts/search-preview.md","slug":"concepts/search-preview.html"}}},"pathContext":{"slug":"concepts/search-preview.html"}}

